.calculate {
	margin-top: 160px;
	@media (max-width: 992px) {
		margin-top: 130px;
	}
	@media (max-width: 567px) {
		margin-top: 90px;
	}
	//   &-SLA {
	//     display: none !important;
	//   }
	&-1 {
		&__left {
			margin-right: 80px;
			@media (max-width: 1200px) {
				margin-right: 40px;
			}
			@media (max-width: 992px) {
				margin-right: 30px;
			}
			@media (max-width: 767px) {
				margin: auto;
			}
		}
		&__right {
			width: 100%;
			position: relative;
		}
		@media (max-width: 767px) {
			flex-direction: column;
		}
	}
	&-2 {
		&__wrap {
			display: flex;
			width: 100%;
			justify-content: space-between;
			min-height: 200px;
			// @media (max-width: 1200px) {
			// 	min-height: 295px;
			// }
			@media (max-width: 992px) {
				flex-direction: column;
			}
			&-sla {
				display: none;
				&.active {
					display: flex;
				}
			}
			&-fdm {
				display: none;
				&.active {
					display: flex;
				}
			}
		}
	}
	&-3 {
		&__wrap {
			display: flex;
			width: 100%;
			justify-content: center;
			min-height: 200px;
			// @media (max-width: 1200px) {
			// 	min-height: 295px;
			// }
			@media (max-width: 767px) {
				min-height: 150px;
			}
		}
		.callback__form {
			max-width: 360px;
			margin: 35px auto 0;
			@media (max-width: 767px) {
				height: fit-content;
				padding: 12px;
				max-width: 300px;
			}
		}
	}
	img {
		width: auto;
	}
	.img-folder {
		position: absolute;
		transform: translateY(-50%);
		top: 50%;
		left: 16px;
	}
	.img-file {
		transform: translateY(-50%);
		position: absolute;
		right: 15px;
		top: 50%;
		@media (max-width: 1200px) {
			top: 45px;
		}
		@media (max-width: 480px) {
			display: none;
		}
	}
	&__wrap {
		background: #2c2c2c;
		border-radius: 12px;
		padding: 70px 70px 90px 70px;
		min-height: 548px;
		position: relative;
		@media (max-width: 992px) {
			padding: 45px 45px 45px;
		}
		@media (max-width: 767px) {
			padding: 30px;
		}
		.callback__form {
			position: relative;
		}
	}
	&__title {
		color: #fff;
		text-align: left;
		display: inline-block;
		margin-bottom: 50px;
		@media (max-width: 992px) {
			margin-bottom: 30px;
		}
	}
	&__content {
		// display: flex;
		display: none;
		&-title {
			font-size: 18px;
			font-style: normal;
			font-weight: 400;
			line-height: 22px;
			letter-spacing: 0em;
			text-align: left;
			color: #fff;
			display: block;
			&-wrap {
				display: flex;
				align-items: center;
				height: 26px;
			}
		}
		&.active {
			display: block;
		}
		&-wrap {
			display: flex;
			width: 100%;
			@media (max-width: 767px) {
				flex-direction: column;
			}
		}
	}
	.question-wrap {
		width: 26px;
		height: 26px;
		background: #000;
		border-radius: 50%;
		margin-left: 10px;
		img {
			margin-left: 8px;
		}
	}
	.step-text {
		font-size: 24px;
		font-style: normal;
		font-weight: 400;
		line-height: 29px;
		letter-spacing: 0em;
		text-align: left;
		color: #fff;
	}
	&__type {
		display: flex;
		margin-top: 32px;
		position: relative;
		button {
			width: 100px;
			height: 50px;
			transition: 0s;
			@media (max-width: 767px) {
				height: 40px;
			}
		}
		&-btn-s {
			background: #9f9f9f !important;
			margin-left: 20px;
			color: #fff !important;
			box-shadow: 9px 16px 16px -6px rgba(159, 159, 159, 0.31) !important;
			&:hover {
				box-shadow: none !important;
			}
			&.active {
				border: 4px solid $accent-color !important;
				box-shadow: none !important;
			}
		}
		&-btn-f {
			&.active {
				border: 4px solid rgb(159, 159, 159) !important;
				box-shadow: none !important;
			}
		}
		@media (max-width: 767px) {
			margin-top: 10px;
			margin-bottom: 30px;
		}
	}
	&__next {
		margin-left: 30px;
		@media (max-width: 767px) {
			margin-left: 10px;
		}
	}
	&__prev {
		border: 2px solid #464646 !important;
		color: #fff !important;
		background: transparent !important;
		box-shadow: none !important;
		font-family: "TTFirsNeueRegular", sans-serif !important;
		&:hover {
			border-color: $accent-color !important;
		}
	}
	.step-wrap {
		display: flex;
		align-items: center;
		img {
			margin-left: 20px;
		}
		@media (max-width: 767px) {
			margin-top: 30px;
		}
	}
	&__footer {
		justify-content: space-between;
		display: flex;
		align-items: center;
		margin-top: 15px;
		button {
			width: 170px;
			height: 60px;
			@media (max-width: 767px) {
				height: 44px;
				width: 125px;
			}
		}
		@media (max-width: 767px) {
			flex-direction: column-reverse;
		}
	}
	&__link-wrap {
		border: 2px solid #404040;
		box-sizing: border-box;
		border-radius: 12px;
		min-height: 60px;
		display: flex;
		align-items: center;
		padding-left: 22px;
		margin-top: 15px;
	}
}

.input-file-wrap {
	border: 2px dashed #404040;
	box-sizing: border-box;
	border-radius: 12px;
	margin-top: 14px;
	position: relative;
	min-height: 90px;
	cursor: pointer;
	label {
		cursor: pointer;
		position: absolute;
		width: 213px;
		height: 50px;
		transform: translateY(-50%);
		top: 50%;
		left: 22px;
		background: #54ec7e;
		border-radius: 8px;
		padding-left: 60px;
		line-height: 50px;
		font-family: "TTFirsNeueMedium", sans-serif;
		box-shadow: 9px 16px 16px -6px rgba(84, 236, 126, 0.31) !important;
		transition: 0.3s;
		&:hover {
			box-shadow: none !important;
		}
		@media (max-width: 1200px) {
			top: 45px;
		}
		@media (max-width: 767px) {
			height: 45px;
		}
	}
	.text-file {
		font-style: normal;
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		color: #ffffff;
		opacity: 0.5;
		max-width: 347px;
		width: 100%;
		display: block;
		position: absolute;
		transform: translateY(-50%);
		top: 50%;
		left: 38%;
		@media (max-width: 1200px) {
			top: 75%;
			left: 22px;
		}
		@media (max-width: 480px) {
			max-width: 250px;
		}
	}
	&.is-active {
		border-color: $accent-color;
	}
	&:hover .btn-file {
		box-shadow: none !important;
	}
	@media (max-width: 1200px) {
		height: 150px;
	}
}
.input-link {
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 17px;
	color: #ffffff;
	padding-left: 15px;
	width: 100%;
	display: block;
	background: transparent;
	border: none;
	padding-left: 15px;
	color: #fff;
	@media (max-width: 1200px) {
		height: 90px;
	}
	@media (max-width: 480px) {
		margin-bottom: 14px;
		height: 86px;
		margin-top: -22px;
	}
	&::-webkit-input-placeholder {
		/* Chrome/Opera/Safari */
		color: #ffffff;
		opacity: 0.5;
		white-space: pre-wrap;
	}
	&::-moz-placeholder {
		/* Firefox 19+ */
		color: #ffffff;
		opacity: 0.5;
		white-space: pre-wrap;
	}
	&:-ms-input-placeholder {
		/* IE 10+ */
		color: #ffffff;
		opacity: 0.5;
		white-space: pre-wrap;
	}
	&:-moz-placeholder {
		/* Firefox 18- */
		color: #ffffff;
		opacity: 0.5;
		white-space: pre-wrap;
	}
}
.file-return {
	position: absolute;
	top: -20px;
	font-size: 13px;
}
input[type="file"] {
	color: transparent;
	height: 100%;
	position: absolute;
	z-index: 10;
	&:hover {
		cursor: pointer;
	}
}
.fileUploadInput input[type="file"]::-webkit-file-upload-button {
	visibility: hidden;
	padding: 0;
	height: 56px;
	width: 0px;
}
.question-wrap {
	cursor: pointer;
}

