.select {
	position: relative;
	margin-top: 34px;
	@media (max-width: 992px) {
		margin: 20px 0 30px;
	}
}
select {
	/* Reset Select */
	appearance: none;
	outline: 0;
	border: 0;
	box-shadow: none;
	/* Personalize */
	flex: 1;
	padding: 0 1em;
	color: #fff;
	background-image: none;
	cursor: pointer;
	background: #404040;
	border: 2px solid #404040;
	box-sizing: border-box;
	border-radius: 12px;
	width: 100%;
	height: 60px;

	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 22px;

	color: #fff;
	@media (max-width: 767px) {
		height: 44px;
	}
}

.select::after {
	content: " ";
	background-image: url(img/arr-select.svg);
	background-repeat: no-repeat;
	position: absolute;
	top: 0;
	right: 0;
	padding: 1em;
	transition: 0.25s all ease;
	pointer-events: none;
	width: auto;
	height: 100%;
	background-position-y: 50%;
}
